html body{
  font-size: 14px !important;
  font-family: Verdana, sans-serif !important;
}

.wat-textContent {
  margin-top: 1rem;
  white-space: pre-wrap;
  line-height: 2rem;
  overflow: scroll; 
  height: 85vh;
  padding: 20px;
  border: 1px;
  border-style: dashed;
  border-color: #999;

  word-wrap: break-word;
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
}

.wat-bioText {
  line-height: initial;
  font-size: 12px;
}

.wat-link {
  color: cornflowerblue;
  font-size: 12px;
  float: right;
  padding-top: 0px;
  padding-bottom: 0px;
  border-top: 2px solid transparent !important;
}


.wat-buttongroup {
  margin-left: 20px;
}

.wat-button {
  border: 0px;
  border-radius: 5px !important;
  font-size: 13px;
}

.wat-button.active {
  border: 3px !important;
  border-style: ridge !important;
  border-color: lightgrey !important;
  border-radius: 50% !important;
  font-weight: bold;
}

.wat-button:hover {
  background-color: lightslategrey;
}

.wat-container {
  margin-top: 10px;

}

.wat-ent-entity {
  position: relative;
  display: inline-block;
  height: auto;
  vertical-align: top;
  text-align: center;
  margin-top: -5px;
  margin-bottom: 10px;
  border-radius: 6px;
  border: 2px solid;
}

.wat-ent-entity span {
  position: relative;
  display: flex;
  font-weight: bold;
  vertical-align: middle;
  align-items: center;
  justify-content: center;
}

.wat-ent-words {
  padding-left: 5px;
  padding-right: 5px;
}

.wat-ent-ner {
  font-size: 12px;
  color: white;
  padding-left: 5px;
  padding-right: 5px;
}
